.sprite:before,
.sprite__logo:before,
.sprite__arrow:before,
.sprite__piple:before,
.sprite__search:before,
.sprite__moon:before,
.sprite__star:before {
  content: ' ';
  background-image: url("../images/sprite/svg-sprite.svg");
  background-repeat: no-repeat;
  background-size: 2.875em 2.6875em;
  display: inline-block;
}
.no-svg .sprite:before,
.no-svg .sprite__logo:before,
.no-svg .sprite__arrow:before,
.no-svg .sprite__piple:before,
.no-svg .sprite__search:before,
.no-svg .sprite__moon:before,
.no-svg .sprite__star:before {
  background-image: url("../images/sprite/svg-sprite.png");
}
.sprite__logo:before {
  background-position: 0em 0em;
  width: 2.0625em;
  height: 1.5625em;
}
.sprite__arrow:before {
  background-position: -2.0625em 0em;
  width: 0.8125em;
  height: 1.25em;
}
.sprite__piple:before {
  background-position: 0em -1.5625em;
  width: 0.4375em;
  height: 1.125em;
}
.sprite__search:before {
  background-position: -0.4375em -1.5625em;
  width: 0.875em;
  height: 0.875em;
}
.sprite__moon:before {
  background-position: -1.3125em -1.5625em;
  width: 0.8125em;
  height: 0.875em;
}
.sprite__star:before {
  background-position: -2.125em -1.5625em;
  width: 0.6875em;
  height: 0.6875em;
}
.header__prev {
  width: 3.125em;
  height: 3.125em;
  line-height: 3.125em;
}
.header-nav {
  height: 3em;
}
html,
body {
  min-height: 100%;
}
body {
  margin: 0;
  padding: 0;
  background-image: url("../images/layer-4.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 16px;
}
